import {useState} from '@wordpress/element';
import { StyledProvider }   from "@lapilli-ui/styles";
import { __experimentalDocumentProvider as DocumentProvider } from "@lapilli-ui/components";

const Root = ( { children } ) => {
	const [doc, setDoc] = useState();

	return <div ref={( node ) => node && node?.ownerDocument && setDoc( node.ownerDocument )}>
		{doc &&
		 <DocumentProvider document={doc}>
			 <StyledProvider document={doc}>
				 {children}
			 </StyledProvider>
		 </DocumentProvider>
		}
	</div>
};

export default Root;